import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { Button, SwipeableDrawer } from "@material-ui/core";
import { Link } from "react-router-dom";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import Brightness6Icon from "@material-ui/icons/Brightness6";
import {
  ItemsPurchasedContext,
  ThemeContext,
} from "../components/context/Contexts";
import "./navstyle.css";
import { useContext, useEffect } from "react";
const Navbar = () => {
  const { currenttheme, setCurrentTheme } = useContext(ThemeContext);
  const { purchaedItems, setPurchasedItems } = useContext(
    ItemsPurchasedContext
  );
  const pSet = new Set(purchaedItems);
  // useEffect(() => {
  //   localStorage.setItem("theme", currenttheme);
  // }, [currenttheme]);
  function setTheme() {
    localStorage.setItem("theme", currenttheme);
    setCurrentTheme(currenttheme ? false : true);
    console.log(currenttheme);
  }
  return (
    <AppBar className="appbar" color="primary" position="sticky">
      <Toolbar className="Toolbar">
        <Link to="/" className="logo">
          <Typography variant="h4">The Apparels Shop</Typography>
        </Link>
        <div className="links-wrapper">
          <Link className="links" to="/shop">
            <Button color="secondary" size="large">
              Shop
            </Button>
          </Link>

          <Button color="secondary" onClick={() => setTheme()}>
            <Brightness6Icon className="themeIcon" />
          </Button>
          <Link className="links" to="/cart">
            <Button size="large">
              <ShoppingCartIcon
                color="secondary"
                // fontSize="large"
                className="shoppingCartIcon"
              />
              {pSet.size ? (
                <span className="notification">{pSet.size}</span>
              ) : (
                ""
              )}
            </Button>
          </Link>
        </div>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
