import { Button, CircularProgress, Typography } from "@material-ui/core";
import { useContext, useState } from "react";
import Db from "../components/db";
import { ItemsPurchasedContext } from "./context/Contexts";
import "./product.css";

const Product = (props) => {
  const { purchaedItems, setPurchasedItems } = useContext(
    ItemsPurchasedContext
  );
  const [loading, setLoading] = useState(false);
  const [AddedtoCart, setAddedtoCart] = useState(false);
  function startLoad(item) {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      setAddedtoCart(true);
      setPurchasedItems([...purchaedItems, item]);
      setTimeout(() => {
        setAddedtoCart(false);
      }, 1500);
    }, 1000);
  }
  const findItem = Db.find((e) => e.id === +props.match.params.id);
  console.log(findItem);
  return (
    <div className="product-wrapper">
      <div className="product-info">
        <Typography variant="h3" color="primary">
          {findItem.name}
        </Typography>
        <Typography color="textSecondary">{findItem.price}</Typography>
        <Typography>
          Lorem ipsum dolor sit amet consectetur adipisicing elit.
          Exercitationem, architecto sit? Nemo nisi excepturi magni officia
          laboriosam. Numquam, laboriosam, reprehenderit eum repellat veritatis
          voluptas quae aliquam laborum asperiores harum nam.
        </Typography>
        <div className="item-buy-popup">
          <Button
            variant="contained"
            color="primary"
            onClick={() => startLoad(findItem)}
          >
            {loading ? (
              <CircularProgress color="textPrimary" fontSize="small" />
            ) : (
              "Buy Now"
            )}{" "}
          </Button>
          <div>
            {AddedtoCart && (
              <div className="item-popup">
                <Typography color="primary">Item added!!</Typography>
              </div>
            )}
          </div>
        </div>
      </div>
      <div className="product-display-img">
        <img src={findItem.img} alt="img" />
      </div>
    </div>
  );
};

export default Product;
