import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";
import { Button, Typography } from "@material-ui/core";
import { useContext, useEffect, useState } from "react";
import { totalPurchaseContext } from "../components/context/Contexts";
import "./cart.css";

const CartPurchase = (props) => {
  const { totalPurchase, setTotalPurchase } = useContext(totalPurchaseContext);
  const [Qty, setQty] = useState(+1);
  const [itemTotal, setItemToal] = useState(+props.items.price.substring(1));
  function removeItem(e) {
    props.setItemsArr(props.itemsArr.filter((item) => item !== e));
  }
  useEffect(() => {
    setItemToal((props.items.price.substring(1) * Qty).toFixed(2));
  }, [Qty]);
  useEffect(() => {
    setTotalPurchase(...[props.items.price.substring(1), itemTotal]);
    props.total(itemTotal);
  }, [itemTotal]);
  function addQty(e) {
    setQty(Qty + 1);
  }
  function minusdQty(e) {
    if (Qty === 1) {
      // console.log(e);
      props.setItemsArr(props.itemsArr.filter((item) => item !== e));
      setItemToal(props.items.price.substring(1));
    } else {
      setQty(Qty - 1);
    }
  }
  return (
    <div>
      <div className="cart-item-wrapper" key={props.items.id}>
        <div className="cart-item">
          <h3>{props.value.indexOf(props.items) + 1}.</h3>
          <h3>{props.items.name}</h3>
          <h3>
            <button
              className="qty-btn"
              // size="small"
              variant="outlined"
              onClick={() => addQty(props.items)}
            >
              <AddIcon className="qty-btn-icon" />
            </button>
            {Qty}
            <button
              className="qty-btn"
              // size="small"
              variant="outlined"
              onClick={() => minusdQty(props.items)}
            >
              <RemoveIcon className="qty-btn-icon" />
            </button>
          </h3>
          <h3>${+itemTotal ?? props.items.price}</h3>
          <Button
            className="remove-btn-Cart"
            onClick={() => removeItem(props.items)}
            style={{
              color: "red",
            }}
          >
            Remove
          </Button>
        </div>
        <Typography color="primary">
          <div className="item-divider"></div>
        </Typography>
      </div>
    </div>
  );
};

export default CartPurchase;
