import { Button, Typography } from "@material-ui/core";
import { useContext, useEffect, useState } from "react";
import {
  ItemsPurchasedContext,
  totalPurchaseContext,
} from "../components/context/Contexts";

import CartPurchase from "./cartPurchase";
import "./cart.css";
const Cart = () => {
  const { totalPurchase, setTotalPurchase } = useContext(totalPurchaseContext);
  const { purchaedItems, setPurchasedItems } = useContext(
    ItemsPurchasedContext
  );

  const pSet = new Set(purchaedItems);
  const purchasedNonRepeat = Array.from(pSet);
  const [total, setTotal] = useState(0);
  let arr = [];
  function addTotal(e) {
    arr.push(+e);
    console.log(e);
    setTotal(arr.reduce((a, b) => a + b));
  }
  function checkout() {
    if (arr.length !== 0) {
      alert("Thanks for shopping, Your Total is " + total.toFixed(2));
      arr = null;
      setPurchasedItems(null);
      setTotal(0);
    }
  }

  useEffect(() => {
    console.log("arr : " + arr);
    console.log("totalPurchase : " + totalPurchase);
  });

  return (
    <div>
      <div className="hero-banner-cart">
        <Typography variant="h3" color="primary">
          Your Cart
        </Typography>
      </div>
      <div>
        <div className="cart-div">
          {purchasedNonRepeat.length ? (
            purchasedNonRepeat.map((item) => (
              <CartPurchase
                items={item}
                value={purchasedNonRepeat}
                total={addTotal}
                setItemsArr={setPurchasedItems}
                itemsArr={purchaedItems}
              />
            ))
          ) : (
            <Typography>Your cart is empty :(</Typography>
          )}
          <div className="checkout-section">
            <Typography variant="h6">Total: ${total.toFixed(2)}</Typography>
            <Button
              className="checkout-btn"
              variant="contained"
              onClick={() => checkout()}
            >
              Checkout
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cart;
