import "./shopStyle.css";
import {
  Typography,
  Input,
  Button,
  InputLabel,
  FormControl,
  Select,
  MenuItem,
  Card,
  CardActionArea,
  CardContent,
  CardActions,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import FavoriteIcon from "@material-ui/icons/Favorite";
import { useRef, useState } from "react";
import { useContext } from "react";
import { ItemsContext } from "../components/context/Contexts";
import Db from "../components/db";
import { Link } from "react-router-dom";
import { ItemsPurchasedContext } from "../components/context/Contexts";
import MagnifyIcon from "@material-ui/icons/Search";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));
const Shop = () => {
  const choice = useRef();
  const classes = useStyles();
  const [type, setType] = useState("all");
  const { items, setItems } = useContext(ItemsContext);
  const { purchaedItems, setPurchasedItems } = useContext(
    ItemsPurchasedContext
  );
  let newWidth = window.innerWidth;
  const [windowSize, setWindowSize] = useState(newWidth <= 900);
  const [extraItemsDisplay, setExtraItemsDisplay] = useState(false);

  let showItems = useRef();
  function handleExpand() {
    if (extraItemsDisplay) {
      setExtraItemsDisplay(false);
      if (newWidth <= 900) {
        if (extraItemsDisplay) {
          showItems.style.display = "block";
          console.log("block");
        }
        if (!extraItemsDisplay) {
          showItems.style.display = "none";
          console.log("none");
        }
      }
    }
  }
  const [searchFilter, setSearchFilter] = useState(null);
  const [randomizeItems, setRandomizeItems] = useState(
    items
      .map((value) => ({ value, sort: Math.random() }))
      .sort((a, b) => a.sort - b.sort)
      .map(({ value }) => value)
  );
  const handleChange = (event) => {
    setType(event.target.value);
    if (event.target.value !== "all") {
      setRandomizeItems(Db.filter((e) => e.type === event.target.value));
    }
    if (event.target.value === "all") {
      return setRandomizeItems(
        items
          .map((value) => ({ value, sort: Math.random() }))
          .sort((a, b) => a.sort - b.sort)
          .map(({ value }) => value)
      );
    }
    console.log(event.target.value);
  };
  function handleInput(e) {
    setSearchFilter(e.target.value);
  }
  function buyItem(item, e) {
    setPurchasedItems([...purchaedItems, item]);
    console.log((e.target.innerHTML = "Added to Cart!"));
    e.target.style.color = "green";
  }
  function resetFilters(e) {
    setRandomizeItems(items);
    e.target.parentNode.parentNode.childNodes[0].childNodes[1].firstElementChild.value =
      "";
    setType("all");
  }
  function filterSearch() {
    if (searchFilter) {
      setRandomizeItems(
        Db.filter((event) =>
          event.name.toLowerCase().includes(searchFilter.toLowerCase().trim())
        )
      );
    }
    if (!searchFilter) {
      setRandomizeItems(items);
    }

    console.log(searchFilter);
  }
  return (
    <div className="shop-container">
      <div className="list">
        <div className="front-font">
          <Typography variant="h3" color="primary">
            Shop with{" "}
            <FavoriteIcon fontSize="large" className="favoriteIconSHop" />{" "}
            things that you like!
          </Typography>
        </div>

        <div className="extraItems" ref={showItems}>
          <div className="shop-search-bar">
            <Typography color="textSecondary" className="SearchBarfirstChild">
              Search your product
            </Typography>
            <Input onKeyUp={handleInput} type="text" />
            <button onClick={filterSearch}>
              <MagnifyIcon />
            </button>
          </div>
          <div>
            <FormControl className={classes.formControl}>
              <InputLabel id="demo-simple-select-label"></InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={type}
                onChange={handleChange}
                ref={choice}
              >
                <MenuItem value={"all"}>All</MenuItem>
                <MenuItem value={"shirt"}>Shirts</MenuItem>
                <MenuItem value={"jacket"}>Jackets</MenuItem>
                <MenuItem value={"accessories"}>Accessories</MenuItem>
              </Select>
            </FormControl>
          </div>
          <Button
            variant="outlined"
            color="primary"
            className="shop-reset-filters"
            onClick={(e) => resetFilters(e)}
          >
            Reset Filter
          </Button>
        </div>
        {/* {windowSize && (
          <div className="extraLineBtn">
            <Button onClick={(e) => handleExpand(e)}>
              <ExpandMoreIcon />
            </Button>
          </div>
        )} */}
      </div>
      <div className="items">
        {randomizeItems.map((item) => (
          <Card className="card" key={item.id}>
            <CardActionArea>
              <Link to={`/shop/${item.id}`}>
                <div className="shop-item-img-wrapper">
                  <img className="shop-item-img" src={item.img} alt="img" />
                </div>
              </Link>
              <CardContent>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="h2"
                  color="primary"
                >
                  {item.name}
                  <Typography color="textSecondary">{item.price}</Typography>
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                  Deserunt, tenetur cupiditate quis alias libero suscipit.
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button
                size="small"
                color="primary"
                onClick={(e) => buyItem(item, e)}
              >
                Buy
              </Button>
              <Link to={`/shop/${item.id}`}>
                <Button size="small" color="primary">
                  Details
                </Button>
              </Link>
            </CardActions>
            <Typography color="primary">
              <div className="shop-item-divider"></div>
            </Typography>
          </Card>
        ))}
      </div>
      {!randomizeItems.length && (
        <Typography variant="h4" className="nothing-found-shop-text">
          Nothing found!
          <br /> Enjoy this bear üêª
        </Typography>
      )}
    </div>
  );
};

export default Shop;
