import "./App.css";
import Home from "./components/home";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Shop from "./components/shop";
import Navbar from "./components/navbar";
import Product from "./components/product";
import Cart from "./components/cart";
import { createTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";
import { purple, red } from "@material-ui/core/colors";
import {
  ThemeContext,
  ItemsContext,
  ItemsPurchasedContext,
  totalPurchaseContext,
} from "./components/context/Contexts";
import { useEffect, useState } from "react";

import Db from "./components/db";
function App() {
  const [currenttheme, setCurrentTheme] = useState(
    localStorage.getItem("theme")
  );
  const [items, setItems] = useState(Db);
  useEffect(() => {
    console.log("theme is " + localStorage.getItem("theme"));
  }, []);
  const [purchaedItems, setPurchasedItems] = useState([]);
  const [totalPurchase, setTotalPurchase] = useState([]);
  const theme = createTheme(
    currenttheme
      ? {
          palette: {
            primary: {
              main: purple[500],
            },
            secondary: {
              main: "#fff",
            },
            error: {
              main: red[500],
            },
          },
        }
      : {
          palette: {
            primary: {
              main: "#000",
            },
            secondary: {
              main: "#fff",
            },
            error: {
              main: red[500],
            },
          },
        }
  );

  return (
    <div className="App">
      <BrowserRouter>
        <ThemeContext.Provider value={{ currenttheme, setCurrentTheme }}>
          <totalPurchaseContext.Provider
            value={{ totalPurchase, setTotalPurchase }}
          >
            <ItemsPurchasedContext.Provider
              value={{ purchaedItems, setPurchasedItems }}
            >
              <ItemsContext.Provider value={{ items, setItems }}>
                <ThemeProvider theme={theme}>
                  <Navbar />
                  <Switch>
                    <Route
                      exact
                      path={["/", "/ApparelsShop"]}
                      component={Home}
                    />
                    <Route exact path="/shop" component={Shop} />
                    <Route exact path="/shop/:id" component={Product} />
                    <Route exact path="/cart" component={Cart} />
                  </Switch>
                </ThemeProvider>
              </ItemsContext.Provider>
            </ItemsPurchasedContext.Provider>
          </totalPurchaseContext.Provider>
        </ThemeContext.Provider>
      </BrowserRouter>
    </div>
  );
}

export default App;
